<template>
  <!-- 虚拟会议 -->
  <div class="meeting">
    <div class="title">
      <div class="text">当前会议室在线人数：{{personNum}}</div>
    </div>
    <div class="read">
      <img src="@/assets/images/meeting/read@2x.png" alt="" />
    </div>
    <div class="lean">
      <img src="@/assets/images/meeting/lean@2x.png" alt="" />
    </div>
    <div class="tactics">
      <img src="@/assets/images/meeting/tactics@2x.png" alt="" />
    </div>
    <div class="more" @click="showMore">
      <img src="@/assets/images/meeting/more@2x.png" alt="" />
    </div>
    <div class="spacecraft animated">
      <img
        class="space"
        src="@/assets/images/meeting/spacecraft@2x.png"
        alt=""
      />
      <img class="light" src="@/assets/images/meeting/light@2x.png" alt="" />
    </div>
    <div :class="'people '+personNumClass"></div>
    <Modal class="modal" :visible.sync="isModalShow" title="更多内容等你探索">
      <div class="modal-content more-modal-content" slot="content">
        <div class="more-btns">
          <div class="btn" v-for="(item,index) in moreData" :key="index"><a :href="item.url">{{item.title}}</a></div>
        </div>
      </div>
      <!-- <div class="modal-footer-row-two" slot="footer">
        <img
          @click="cancel"
          src="@/assets/images/common/modal_btn_qx.png"
          alt=""
        />
        <img
          @click="confirm"
          src="@/assets/images/common/modal_btn_qd.png"
          alt=""
        />
      </div> -->
    </Modal>
  </div>
</template>
<script>
import Modal from '@/components/commonModal'
export default {
  name: 'Shop',
  data () {
    return {
      userInfo: JSON.parse(localStorage.getItem('userInfo')).data,
      moreData: [],
      personNum: 0,
      isModalShow: false
    }
  },
  components: {
    Modal
  },
  created () {
    // 获取人数
    this.getPersonNum()
  },
  computed: {
    personNumClass () {
      if (this.personNum > 15000) {
        return 'num-15000'
      } else if (this.personNum > 12000 && this.personNum <= 15000) {
        return 'num-12000'
      } else if (this.personNum > 10000 && this.personNum <= 12000) {
        return 'num-10000'
      } else if (this.personNum > 5000 && this.personNum <= 10000) {
        return 'num-5000'
      } else {
        return 'num-3000'
      }
    }
  },
  methods: {
    async showMore () {
      const res = await this.$request('get', '/activity/getmoremeetinglist')
      this.moreData = res.data
      this.isModalShow = true
    },
    async getPersonNum () {
      const res = await this.$request('post', '/user/meetingonlinecount', {uid: this.userInfo.uid})
      this.personNum = res.data
    }
  }
}
</script>
<style lang="less" scoped>
.meeting {
  height: 100vh;
  background-image: url(~@/assets/images/meeting/meeting_bg@2x.png);
  background-repeat: no-repeat;
  background-size: 100% 100%;
  background-position: center;
  position: relative;
  .title {
    width: 510px;
    height: 154px;
    background-image: url(~@/assets/images/meeting/title@2x.png);
    background-repeat: no-repeat;
    background-size: 100%;
    background-position: center;
    position: absolute;
    top: 44px;
    left: 108px;
    .text {
      font-size: 20.8px;
      color: #ba2900;
      position: absolute;
      width: 100%;
      text-align: center;
      bottom: 15px;
    }
  }
  .read {
    position: absolute;
    top: 191px;
    right: 50px;
    img {
      width: 476px;
      height: 325px;
    }
  }
  .lean {
    position: absolute;
    top: 516px;
    right: 0px;
    img {
      width: 301px;
      height: 288px;
    }
  }
  .tactics {
    position: absolute;
    top: 576px;
    left: 0px;
    img {
      width: 287px;
      height: 265px;
    }
  }
  .more {
    position: absolute;
    top: 327px;
    left: 0px;
    img {
      width: 236px;
      height: 181px;
    }
  }
  .spacecraft {
    position: absolute;
    bottom: 400px;
    left: 251px;
    &.animated {
      animation: float 4s linear infinite alternate;
    }
    @keyframes float {
      0%,100% {
        transform: translate(0,0)
      }
      30%{
        transform: translate(15px,3px)
      }
      60% {
        transform: translate(35px,8px)
      }
      60% {
        transform: translate(18px,5px)
      }
    }
    img.space {
      width: 285px;
      height: 257px;
    }
    img.light {
      width: 422px;
      height: 220px;
      position: absolute;
      top: 207px;
      left: 50%;
      transform: translateX(-50%);
    }
  }
  .people {
    width: 750px;
    height: 364px;
    background-repeat: no-repeat;
    background-size: 100%;
    background-position: center;
    position: absolute;
    bottom: 0;
    &.num-3000 {
      background-image: url(~@/assets/images/meeting/person_3000@2x.png);
    }
    &.num-5000 {
      background-image: url(~@/assets/images/meeting/person_5000@2x.png);
    }
    &.num-10000 {
      background-image: url(~@/assets/images/meeting/person_10000@2x.png);
    }
    &.num-12000 {
      background-image: url(~@/assets/images/meeting/person_12000@2x.png);
    }
    &.num-15000 {
      background-image: url(~@/assets/images/meeting/person_15000@2x.png);
    }
  }
  .more-modal-content {
    .more-btns {
      padding-bottom: 40px;
      .btn {
        width: 398px;
        height: 92px;
        line-height: 92px;
        border-radius: 24px;
        border: 1px solid #fe6f5a;
        font-size: 36px;
        color: #333;
        font-weight: 600;
        text-align: center;
        margin-left: auto;
        margin-right: auto;
        margin-bottom: 20px;
        &:last-child{
          margin-bottom: 0;
        }
        a,a:active,a:visited {
          color: #333;
          text-decoration: none;
        }
      }
    }
  }
}
</style>



